import { Fade, Grow, Icon, Link } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { NextPage } from "next";
import * as React from "react";
import { useState } from "react";
import PageComponent from "../components/page";
import { AppStoreLink, GooglePlayLink } from "../utils/app-stores";

const useStyles = makeStyles(() => ({
  topImg: {
    position: "absolute"
  },
  bottomImg: {
    width: 269.1,
    height: 532.8
  },

  imageIcon: {
    width: 158.625,
    height: 47.8125
  },
  iconRoot: {
    textAlign: "center"
  }
}));

const IndexPage: NextPage = () => {
  const classes = useStyles();
  const [phoneLoad, changePhoneLoad] = useState(false);

  return (
    <PageComponent showFooter={true} title="Conva ">
      <Grow onEntered={() => changePhoneLoad(true)} timeout={1000} in={true}>
        <div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              marginTop: "2vh",
              justifyContent: "center"
            }}
          >
            <div style={{ width: "100" }}>
              <img
                src={require("../assets/imgs/final.png")}
                className={classes.bottomImg}
              />
            </div>
          </div>

          <Fade timeout={1000} in={phoneLoad}>
            <div style={{ textAlign: "center", marginTop: 20 }}>
              <Link href={AppStoreLink}>
                <Icon classes={{ root: classes.iconRoot }}>
                  <img
                    className={classes.imageIcon}
                    src={require("../assets/imgs/app-store.svg")}
                  />
                </Icon>
              </Link>
              <Link href={GooglePlayLink}>
                <Icon classes={{ root: classes.iconRoot }}>
                  <img
                    className={classes.imageIcon}
                    src={require("../assets/imgs/google-play.png")}
                  />
                </Icon>
              </Link>
            </div>
          </Fade>
        </div>
      </Grow>
    </PageComponent>
  );
};

export default IndexPage;
